import { Component, OnInit, ViewEncapsulation } from '@angular/core';
//import { API_LANDING_PAGE } from 'src/app/core/api/app.api';
import { SeoService } from 'src/app/core/services/header/seo.service';
import { OrderService } from 'src/app/core/services/order.service';

@Component({
  selector: 'app-saibamais',
  templateUrl: './saibamais.component.html',
  styleUrls: ['./saibamais.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SaibamaisComponent implements OnInit {

  constructor(
    private orderService: OrderService,
    private seoService: SeoService
    ) { }

  ngOnInit() {
    this.seoService.createLinkForCanonicalURL();
    const inputs = document.querySelectorAll('input');
    const masks = {
      cpf(value) {
        return value.replace(/\D/g, '').replace(/(\d{3})(\d)/, '$1.$2').replace(/(\d{3})(\d)/, '$1.$2').replace(/(\d{3})(\d{1,2})/, '$1-$2').replace(/(-\d{2})\d+?$/, '$1')
      },
      phone(value) {
        return value.replace(/\D/g, '').replace(/(\d{2})(\d)/, '($1) $2').replace(/(\d{4})(\d)/, '$1-$2').replace(/(\d{4})-(\d)(\d{4})/, '$1$2-$3').replace(/(-\d{4})\d+?$/, '$1')
      }
    }

    inputs.forEach(input => {
      const field = input.dataset.js;

      if (input.type !== 'checkbox') {
        input.addEventListener('input', function() {
          if (field) {
            this.value = masks[field](this.value.toString())
          }
        }, false)
      }
    })
  }

  enviar(event){
    event.preventDefault();

    const form = document.forms[0];
    const name = form.querySelector('#nome');
    const cpf = form.querySelector('#cpf');
    const email = form.querySelector('#email');
    const uf = form.querySelector('#uf');
    const phone = form.querySelector('#telefone');
    const communications = form.querySelector('#aceiteComunicados');
    const terms = form.querySelector('#leuTermos');
    const buttonLabel = form.querySelector('#button-label');

    const data = {
      nome: (<HTMLInputElement>name).value,
      cpf: (<HTMLInputElement>cpf).value,
      email: (<HTMLInputElement>email).value,
      uf: (<HTMLInputElement>uf).value,
      telefone: (<HTMLInputElement>phone).value,
      aceiteComunicados: (<HTMLInputElement>communications).value,
      leuTermos: (<HTMLInputElement>terms).value,
      action: "bitrix",
      title: "Saiba Mais",
      emails: ["fabiosaton@movida.com.br"]
    }

    if ((<HTMLInputElement>name).value == '') { alert('Campo nome obrigatório'); return }
    if ((<HTMLInputElement>email).value == '') { alert('Campo e-mail obrigatório'); return }
    if ((<HTMLInputElement>uf).value == '') { alert('Campo UF obrigatório'); return }
    if ((<HTMLInputElement>phone).value == '') { alert('Campo Telefone obrigatório'); return }
    if ((<HTMLInputElement>terms).checked == false) { alert('Campo Termos obrigatório'); return }

    (<HTMLInputElement>buttonLabel).innerText = 'ENVIANDO...';

    this.orderService.createLandingPage(data).subscribe(response => {
      (<HTMLInputElement>buttonLabel).innerText = 'ENVIAR';
      alert('Dados enviados com sucesso!');
    }, error => {
      (<HTMLInputElement>buttonLabel).innerText = 'ENVIAR';
    });
  }
}
